}

.header.is-scrolled .header__sns-link {
  background-color: var(--white);
  mask-image: url("../images/common/common_instagram.png");
  mask-size: contain;
  mask-repeat: no-repeat;
  mask-position: center;
}

.header.is-scrolled .header__sns-link:nth-child(2) {
  mask-image: url("../images/common/common_facebook.png");
}

.header__sns-icon {
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: opacity 0.3s ease;
}

.header.is-scrolled .header__sns-icon {
  opacity: 0;
}

@media (any-hover: hover) {
  .header__sns-link:hover .header__sns-icon {
