.more::after {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 50%);
  bottom: 0;
  aspect-ratio: 1;
  width: 0.625rem;
  border-radius: 50%;
  background-color: var(--brown);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

@media (any-hover: hover) {
  .more:hover {
    color: var(--brown);
    transform: translateY(-0.125rem);
  }
  .more:hover::after {
    width: 0.875rem;
